.add-poster {
  position: relative;
}
.add-poster {
  height: 33vh;
  position: fixed;
  bottom: 0;
  left: 0;
  right:  0;
  background: white;
  -webkit-box-shadow: 0 -6px 21px 0px rgba(0,0,0,0.125), 0 3px 9px 2px rgba(0,0,0,0.14);
          box-shadow: 0 -6px 21px 0px rgba(0,0,0,0.125), 0 3px 9px 2px rgba(0,0,0,0.14);
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
.add-poster.closed {
  -webkit-transform: translateY(33vh);
      -ms-transform: translateY(33vh);
          transform: translateY(33vh);
}
.toggle {
  background: white;
  color: #ed2e13;
  border-radius: 2px 2px 0 0;
  top: -3rem;
  left: 2rem;
  position: absolute;
  -webkit-box-shadow: 0 -10px 18px -2px rgba(0,0,0,0.125), 0 -6px 6px 0px rgba(0,0,0,0.14);
          box-shadow: 0 -10px 18px -2px rgba(0,0,0,0.125), 0 -6px 6px 0px rgba(0,0,0,0.14);
}
.toggle:hover {
  background: white;
  color: #f43924;
  -webkit-box-shadow: 0 -10px 18px -2px rgba(0,0,0,0.125), 0 -6px 6px 0px rgba(0,0,0,0.14);
          box-shadow: 0 -10px 18px -2px rgba(0,0,0,0.125), 0 -6px 6px 0px rgba(0,0,0,0.14);
}
.toggle:focus {
  outline: 0;
  /* IE10+ */

  /* Mozilla Firefox */

  /* Opera */
  background-image: -o-linear-gradient(bottom, #FFFFFF 0%, #D0CCCC 500%);

  /* Webkit (Safari/Chrome 10) */
  background-image: -webkit-gradient(linear, left bottom, left top, color-stop(0, #FFFFFF), color-stop(500, #D0CCCC));

  /* Webkit (Chrome 11+) */
  background-image: -webkit-linear-gradient(bottom, #FFFFFF 0%, #D0CCCC 500%);

  /* W3C Markup */
  background-image: -webkit-gradient(linear, left bottom, left top, from(#FFFFFF), color-stop(500%, #D0CCCC));
  background-image: linear-gradient(to top, #FFFFFF 0%, #D0CCCC 500%);
}
.toggle .toggle-icon {
  position: relative
}
.toggle .toggle-icon:before {
  content: '';
  display: block;
  width: 2rem;
  height: 0.125rem;
  background-color: #ed2e13;
  position: absolute;
  top: 0;
  left: -0.925rem;
}
.toggle .toggle-icon:after {
  content: '';
  display: block;
  width: 0.125rem;
  height: 2rem;
  background-color: #ed2e13;
  position: absolute;
  top: -1rem;
  left: 0;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;

}
.add-poster.open .toggle-icon:after {
  -webkit-transform:rotate(-90deg);
      -ms-transform:rotate(-90deg);
          transform:rotate(-90deg);
}



